import { createRouter, createWebHistory } from 'vue-router';

import Home from '../Pages/Home.vue';
import AboutUs from '../Pages/AboutUs.vue';
import CoursesList from '../Pages/CoursesList.vue';
import CourseDetail from '../Pages/CourseDetail.vue';
import FrequentQuestions from '../Pages/FrequentQuestions.vue';
import Login from '../Pages/auth/Login.vue';
import SignUp from '../Pages/auth/SignUp.vue';
import CourseVideo from '../Pages/CourseVideo.vue';
import resetPassword from '../Pages/auth/resetPassword.vue';
import store from '../store/index.js';

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/nosotros',
    name: 'AboutUs',
    component: AboutUs
  },
  {
    path: '/cursos',
    name: 'Courses',
    component: CoursesList
  },
  {
    path: '/cursos/:id',
    name: 'Course',
    component: CourseDetail
  },
  {
    path: '/preguntas-frecuentes',
    name: 'FAQ',
    component: FrequentQuestions
  },
  {
    path: '/iniciar-seccion',
    name: 'Login',
    component: Login,
    meta: { requiredUnauth: true }
  },
  {
    path: '/registro',
    name: 'SignUp',
    component: SignUp,
    meta: { requiredUnauth: true }
  },
  {
    path: '/cursos/:id/video/:idVideo',
    name: 'Video',
    component: CourseVideo,
    props: true,
    meta: { requiredAuth: true, requiredPurchasedCourse: true }
  },
  {
    path: '/auth/reset-password',
    name: 'resetPassword',
    component: resetPassword,
    meta: { requiredUnauth: true }
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior() {
    return { left: 0, top: 0 };
  }
});

router.beforeEach((to, from, next) => {
  if (to.meta.requiredAuth && !store.getters.isAuthenticated) {
    next('/iniciar-seccion');
  } else if (to.meta.requiredAuth && store.getters.isAuthenticated) {
    if (to.meta.requiredPurchasedCourse && store.getters.purchasedCourse) {
      next();
    } else if (to.meta.requiredPurchasedCourse) {
      next('/cursos/1');
    } else {
      next('/');
    }
  } else if (to.meta.requiredUnauth && store.getters.isAuthenticated) {
    next('/');
  } else {
    next();
  }
});

export default router;
