<template>
  <the-hero></the-hero>
  <meta-list></meta-list>

  <v-wrapper>
    <div class="middle">
      <iframe
        src="https://player.vimeo.com/video/508165779"
        height="360"
        frameborder="0"
        allow="autoplay; fullscreen"
        allowfullscreen
      ></iframe>
      <section class="text-home">
        <layout-text
          mode="mb-6"
          title="Aprende como tener salud"
          description="Aprende que comer, como ejercitarte, como seleccionar los mejores ejercicios y alimentos que se adapten a tu estilo de vida. Los gimnasios y personal trainer son un lujo en tiempo, no en dinero."
        ></layout-text>
        <layout-text
          title="17 temas"
          description="Trataremos todos los temas de manera básica para que las personas comprendan lo que deben hacer para tener una vida sana"
        ></layout-text>
        <layout-text
          title="Duración 1h y 20 minutos"
          description="Sabemos que no te interesa ser un experto del ejercicio, solo tener el conocimiento para alcanzar una buena salud. Por eso el curso te tomara menos de 2 horas saber todo lo que necesitas."
        ></layout-text>
      </section>
    </div>
  </v-wrapper>
  <service-list></service-list>
  <ad-item></ad-item>
</template>

<script>
import MetaList from "../components/metas/MetaList.vue";
import ServiceList from "../components/service/ServiceList";
import AdItem from "../components/ads/AdItem.vue";
import TheHero from "../components/header/TheHero";

export default {
  titlePage: " ",
  components: {
    MetaList,
    ServiceList,
    AdItem,
    TheHero,
  },
  data() {
    return {
      screenWidth: "",
    };
  },
  computed: {
    baseTextWidth() {
      return this.screenWidth < 750 ? "100" : "40";
    },
  },
  methods: {
    updateScreen() {
      this.screenWidth = screen.width;
    },
  },
  mounted() {
    this.$store.dispatch("closeMenu");
    this.$nextTick(() => {
      window.addEventListener("resize", this.updateScreen);
    });
  },
  created() {
    this.updateScreen();
  },
};
</script>

<style scoped>
iframe {
  width: 100%;
}
@media screen and (min-width: 850px) {
  .middle {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    position: relative;
  }
  iframe {
    position: sticky;
    top: 120px;
  }
}
</style>
