import { createStore } from 'vuex';

import MetasModule from './metas/index';
import AuthModule from './auth/index';
import coursesModule from './courses/index';

const store = createStore({
  state: {
    offset: null,
    isVisible: false,
    navBarSize: false
  },
  mutations: {
    updateOffset(state, payload) {
      if (payload.offsetValue <= 500) {
        state.offset = 25;
      } else if (payload.offsetValue > 500 && payload.offsetValue <= 850) {
        state.offset = 50;
      } else if (payload.offsetValue > 850 && payload.offsetValue <= 1440) {
        state.offset = 100;
      } else if (payload.offsetValue > 1440 && payload.offsetValue <= 1920) {
        state.offset = 200;
      } else {
        state.offset = 300;
      }
    },
    IsMobilNavBar(state, payload) {
      if (payload.offsetValue <= 600) {
        state.navBarSize = true;
      } else {
        state.navBarSize = false;
      }
    },
    openMenu(state) {
      state.isVisible = true;
    },
    closeMenu(state) {
      state.isVisible = false;
    }
  },
  actions: {
    updateOffset(context, data) {
      context.commit('updateOffset', data);
      context.commit('IsMobilNavBar', data);
    },
    closeMenu(context) {
      context.commit('closeMenu');
    },
    openMenu(context) {
      context.commit('openMenu');
    }
  },
  getters: {
    offset(state) {
      return state.offset;
    },
    isVisible(state) {
      return state.isVisible;
    },
    navBarSize(state) {
      return state.navBarSize;
    }
  },
  modules: {
    metas: MetasModule,
    auth: AuthModule,
    course: coursesModule
  }
});

export default store;
