<template>
  <the-menu />
  <router-view />
  <the-footer />
</template>
<script>
import TheMenu from "./components/header/nav/TheMenu.vue";
import TheFooter from "./components/footer/TheFooter.vue";
export default {
  components: {
    TheFooter,
    TheMenu,
  },
  methods: {
    calcMargin() {
      this.$store.dispatch("updateOffset", { offsetValue: screen.width });
    },
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener("resize", this.calcMargin);
    });
  },
  async created() {
    this.$store.dispatch("login", { email: null });
    // await this.$store.dispatch("getCourse");
    this.calcMargin();
  },
};
</script>
