<template>
  <li class="module-item">
    <h3 @click="isSee">
      <i class="fas fa-chevron-right" :class="iActive"></i>{{ video.module }}
    </h3>
    <ul class="lession-list" v-if="see">
      <li
        v-for="leccion in video.lecciones"
        :key="leccion.id"
        class="lession-item"
      >
        <!-- <router-link :to="`${$route.path}/video/${leccion.id}?m=${video.id}`"> -->
        <router-link
          :to="{
            name: 'Video',
            params: { id: 1, idVideo: leccion.id },
            query: { m: video.id },
          }"
          v-if="isPurchased"
          class="course-item"
        >
          <div class="internal">
            <p><i class="far fa-play-circle"></i>{{ leccion.name }}</p>
            <p>{{ leccion.duration }}</p>
          </div>
        </router-link>
        <span v-else class="course-item course-item--disabled">
          <div class="internal">
            <p><i class="far fa-play-circle"></i>{{ leccion.name }}</p>
            <p>{{ leccion.duration }}</p>
          </div>
        </span>
      </li>
    </ul>
  </li>
</template>

<script>
export default {
  props: ["video"],
  data() {
    return {
      see: false,
    };
  },
  computed: {
    iActive() {
      return this.see ? "fa-chevron-right--active" : null;
    },
    isPurchased() {
      const purchase = this.$store.getters.purchasedCourse;
      //-a(purchase);
      return purchase;
    },
  },
  methods: {
    isSee() {
      this.see = !this.see;
    },
  },
};
</script>

<style scoped>
.lession-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.lession-item {
  background: var(--white);
}
.lession-item .course-item {
  font-size: 1.6rem;
  display: block;
  padding: 0.5rem 5rem;
  text-decoration: none;
  color: var(--primary-color);
}

.lession-item .course-item--disabled {
  color: var(--light-grey);
}

.internal {
  display: flex;
  justify-content: space-between;
}

.internal p {
  text-decoration: underline;
}
.lession-item a:hover {
  background: rgba(200, 200, 200, 0.1);
}
.module-item {
  list-style: none;
  box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.1);
  background: rgba(175, 175, 184, 0.1);
  border-radius: 5px;
  margin-bottom: 2rem;
  overflow: hidden;
}
i {
  margin-right: 1rem;
}
.fa-chevron-right {
  font-size: 1.4rem;
  transform: rotate(90deg);
  transition: all 0.3s ease;
}
.fa-chevron-right--active {
  transform: rotate(-90deg);
}
h3 {
  display: flex;
  align-items: center;
  padding: 1rem 0;
  padding-left: 4rem;
  cursor: pointer;
  font-size: 2rem;
  color: #6f6f73;
  /* outline: 5px solid white; */
  margin: 0;
}
</style>