function getTitlePage (vm) {
  const { titlePage } = vm.$options
  if (titlePage) {
    return typeof titlePage === 'function'
      ? titlePage.call(vm)
      : titlePage
  }
}
export default {
  created () {
    const titlePage = getTitlePage(this)
    if (titlePage) {
      document.title = `Fox Grow ${titlePage}`
    }
  }
}