<template>
  <div @click="$emit('close')"></div>
  <dialog open>
    <header>
      <slot name="header">
        <base-title>{{ title }}</base-title>
      </slot>
    </header>
    <section>
      <slot></slot>
    </section>
    <menu>
      <slot name="actions">
        <base-button @click="$emit('close')">Close</base-button>
      </slot>
    </menu>
  </dialog>
</template>

<script>
import BaseTitle from "./BaseTitle.vue";
export default {
  components: { BaseTitle },
  props: {
    title: {
      type: String,
      require: false,
    },
  },
};
</script>

<style scoped>
div {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background-color: var(--primary-color);
  z-index: 250;
}

dialog {
  position: fixed;
  top: 20vh;
  left: 50%;
  width: 50%;
  z-index: 251;
  border-radius: 5px;
  border: none;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  padding: 2rem 4rem;
  margin: 0;
  overflow: hidden;
  transform: translateX(-50%);
  transition: all 1s;
}

header {
  background-color: #fff;
  width: 100%;
  padding: 1rem;
}

header h2 {
  margin: 0;
}

section {
  padding: 1rem;
}

menu {
  padding: 1rem;
  display: flex;
  justify-content: flex-end;
  margin: 0;
}

@media (max-width: 768px) {
  dialog {
    width: 90%;
    padding: 0.5rem 1rem;
  }
  menu {
    justify-content: center;
  }
}
</style>
