<template>
  <div class="mb-6" :style="widthSize">
    <base-title>{{ title }}</base-title>
    <base-description>{{ description }}</base-description>
  </div>
</template>
<script>
export default {
  props: {
    title: {
      type: String,
      required: true,
      default: "",
    },
    description: {
      type: String,
      required: true,
      default: "",
    },
    width: {
      type: String,
      required: false,
      default: "100",
    },
  },
  computed: {
    widthSize() {
      return `width: ${this.width}%`;
    },
  },
};
</script>

<style scoped>
</style>
