import { api } from '../../service/apiFetch';

export default {
  state() {
    return {
      name: null,
      userId: null,
      isAuthenticated: false
    };
  },
  mutations: {
    setUser(state, payload) {
      state.name = payload.name;
      state.userId = payload.userId;
      state.isAuthenticated = true;
    }
  },
  actions: {
    async login(context, payload) {
      if (localStorage.getItem('UserId')) {
        context.commit('setUser', {
          userId: localStorage.getItem('UserId'),
          name: localStorage.getItem('UserName')
        });
      } else if (payload.email !== null) {
        return new Promise((resolve, reject) => {
          api('auth/local', {
            identifier: payload.email,
            password: payload.password
          })
            .then((response) => response.json())
            .then((data) => {
              const nameSplit = data.user.username.split(' ');
              localStorage.setItem('UserId', data.user.id);
              localStorage.setItem('UserName', nameSplit[0]);
              context.commit('setUser', {
                userId: data.user.id,
                name: nameSplit[0]
              });
              return;
            })
            .then(() => {
              resolve(false);
            })
            .catch((err) => {
              reject(err.code);
            });
        });
      }
    },
    async signup(context, payload) {
      //-a(payload);
      try {
        await api('auth/local/register', payload);

        //const data = await response.json();
        //-a(data);
      } catch (err) {
        //-a(err);
      }
    },
    async resetPassword(context, payload) {
      if (payload.code) {
        //firebase.auth().confirmPasswordReset(payload.code, payload.password);
      } else {
        const response = await api('auth/forgot-password', {
          email: payload.email
        });
        await response.json();
        //-a(data.data);
      }
    },
    logout(context) {
      window.localStorage.removeItem('UserId');
      window.localStorage.removeItem('UserName');
      context.commit('setUser', {
        userId: null,
        name: null
      });
      context.dispatch('changePurchasedStatus', { status: false });
    }
  },
  getters: {
    name(state) {
      return state.name;
    },
    userId(state) {
      return state.userId;
    },
    isAuthenticated(state) {
      return state.isAuthenticated;
    }
  }
};
