<template>
  <div class="card" :class="mode" :style="[padding, widthSize, maxWidthSize]">
    <slot />
  </div>
</template>
<script>
export default {
  props: {
    mode: String,
    noPadding: {
      type: Boolean,
      default: false,
    },
    width: {
      type: String,
      default: "100",
    },
    maxWidth: {
      type: String,
      default: "none",
    },
  },
  computed: {
    padding() {
      return this.noPadding ? "padding: 0" : "padding: 2.5rem";
    },
    widthSize() {
      return `width: ${this.width}%`;
    },
    maxWidthSize() {
      return `max-width:${this.maxWidth}px`;
    },
  },
};
</script>
<style scoped>
.card {
  background: #f9f9f9;
  border-radius: 10px;
  box-shadow: 3px 6px 15px rgba(0, 0, 0, 0.2);
}
</style>
