<template>
  <div class="nav" :class="navActive">
    <button @click="closeMenu">
      <i class="fas fa-times"></i>
    </button>
    <ul>
      <li>
        <router-link to="/"> Inicio </router-link>
      </li>
      <li>
        <router-link to="/cursos/1"> Cursos </router-link>
      </li>
      <li>
        <router-link to="/nosotros"> Nosotros </router-link>
      </li>
      <li>
        <router-link to="preguntas-frecuentes"> FAQs </router-link>
      </li>
      <li class="login" v-if="isAuth">
        <router-link :to="{ name: 'Login' }"> iniciar Seccion </router-link>
      </li>
      <li class="auth" v-if="!isAuth">
        <p><i class="fas fa-user"></i>{{ username }}&nbsp;&nbsp;|</p>
        <button @click="logout">Cerra Seccion</button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      auth: this.$store.getters.name ? false : true,
    };
  },
  computed: {
    isAuth() {
      return this.$store.getters.userId ? false : true;
    },
    username() {
      return this.$store.getters.name;
    },
    navActive() {
      const visible = this.$store.getters["isVisible"] ? "nav-active" : null;
      return visible;
    },
  },
  methods: {
    changeset() {
      this.auth = localStorage.getItem("UserId") ? false : true;
    },
    logout() {
      this.$store.dispatch("logout");
      this.$router.replace("/");
    },
    closeMenu() {
      this.$store.dispatch("closeMenu");
      //-a(this.$route.path);
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.changeset();
      this.auth = localStorage.getItem("UserId") ? false : true;
      // this.$forceUpdate();
    });
  },
};
</script>

<style scoped>
ul {
  display: flex;
  list-style: none;
  align-items: center;
  margin: 0;
}

li {
  padding: 1.5rem;
}

i {
  font-size: 1.8rem;
  color: var(--light-grey);
}

button {
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  position: relative;
}
.nav {
  background-color: #8b9ea6;
  position: fixed;
  top: 0;
  left: -102%;
  width: 100vw;
  height: 100vh;
  transition: all 0.3s cubic-bezier(0.2, 0, 1, 0.715);
  opacity: 0;
  z-index: 5;
}

.nav-active {
  left: 0;
  transition: all 0.5s cubic-bezier(0.6, 1.6, 0.7, 0.5);
  opacity: 1;
}

.nav ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: -3.5rem;
}

.nav a {
  font-size: 1.8rem;
  display: block;
  color: var(--white);
  padding: 1rem;
  text-decoration: none;
}
li .router-link-active {
  color: var(--secondary-color);
  font-weight: 700;
  position: relative;
}

.router-link-active::after {
  content: "";
  position: absolute;
  display: block;
  width: 1.2rem;
  height: 0.2rem;
  border-radius: 8px;
  background-color: var(--secondary-color);
  top: 100%;
  left: 48%;
  transform: translateX(-50%);
}
.nav > button {
  position: relative;
  margin: 2rem;
  top: 7px;
  left: 75%;
}
.nav-active .fa-times {
  transform: rotate(-180deg);
  transition: transform 0.5s ease 0.8s;
}
.fa-times {
  color: var(--white);
}

.login {
  background-color: var(--secondary-color);
  padding: 5px 15px 3px 15px;
  border-radius: 5px;

  margin-top: 40px;
}
.login a {
  color: white;
}

.auth {
  display: flex;
  align-items: center;
  margin-bottom: 7px;
  background-color: var(--secondary-color);
  border-radius: 5px;
  margin-top: 40px;
}
.auth p {
  font-size: 1.6rem;
  color: white;
  margin: 0;
}
.fa-user {
  margin-right: 8px;
  position: relative;
  top: 2px;
  color: white;
}

.auth button {
  font-size: 1.6rem;
  color: white;
  cursor: pointer;
  padding: 7px 10px;
}

.auth button:hover {
  background: rgba(255, 255, 255, 0.2);
}
</style>