<template>
  <v-wrapper>
    <div class="header-lession">
      <div class="header-info">
        <div class="header-lession-name">
          <base-title>Entrenamiento y alimentación básica</base-title>
        </div>
        <div class="header-lession-desc">
          <base-title>Descripción</base-title>
          <base-description>
            Aprende cuales son les mejores ejercicios y alimentación para
            incluir en tu rutina diaria y semanal a través de nuestros cursos
          </base-description>
        </div>
        <div class="header-lession-target">
          <base-title>Objetivos</base-title>
          <ul>
            <li>
              <base-description> Alimentarte correctamente </base-description>
            </li>
            <li>
              <base-description>
                Seleccionar los mejores ejercicios para ti
              </base-description>
            </li>
            <li>
              <base-description>
                Obtener una vida más saludable
              </base-description>
            </li>
            <li>
              <base-description>
                Poner ejercitarte donde quieras
              </base-description>
            </li>
          </ul>
        </div>
        <div class="header-lession-lastest">
          <base-title>Duración</base-title>
          <base-description>1h y 20 minutos</base-description>
        </div>
      </div>
      <div class="header-lession-video-container">
        <img
          src="https://res.cloudinary.com/dugzgpblw/image/upload/c_scale,w_502/v1612209923/fox-grow/assets/img/Curso_oqpikw.png"
          alt=""
        />
        <div ref="paypal" v-if="!isPurchased"></div>
        <div class="login" v-if="!user && !isPurchased">
          <router-link :to="{ name: 'Login' }"> iniciar Seccion </router-link>
        </div>
      </div>
    </div>
    <div class="body-lession">
      <base-title>Lecciones</base-title>
      <ul>
        <class-list
          v-for="video in videos"
          :key="video.id"
          :video="video"
        ></class-list>
      </ul>
    </div>
  </v-wrapper>
</template>

<script>
import ClassList from "../components/class/ClassList";

export default {
  components: {
    ClassList,
  },
  computed: {
    videos() {
      return this.$store.getters.videos;
    },
    isPurchased() {
      return this.$store.getters.purchasedCourse;
    },
    user() {
      return this.$store.getters.userId;
    },
  },

  methods: {
    setLoaded() {
      const isId = this.$store.getters.userId;
      //-a(isId);
      window.paypal
        .Buttons({
          createOrder: function (data, actions) {
            return actions.order.create({
              purchase_units: [
                {
                  amount: {
                    value: "10.0",
                  },
                },
              ],
              experience: {
                input_fields: {
                  no_shipping: 1,
                },
              },
            });
          },
          onApprove: function (data, actions) {
            return actions.order.capture().then(function (details) {
              const data = {
                order_url: details.links[0].href,
                user_email: details.payer.email_address,
                order_id: details.id,
                user_recid: isId,
              };
              fetch("http://localhost:1337/paypals", {
                method: "POST",
                headers: {
                  "content-type": "application/json",
                },
                body: JSON.stringify(data),
              });
            });
          },
        })
        .render(this.$refs.paypal);
    },
  },
  mounted() {
    const isId = this.$store.getters.userId;
    //-a(this.isPurchased);
    if (isId) {
      const script = document.createElement("script");
      script.src =
        "https://www.paypal.com/sdk/js?client-id=AT2w3BRzbYprZPVnQLM-foLf0bm2Rx8KAT-PqCK1EpZRj7n2J0VtazuYW64HkHcjVutZqZplo_SeTtty";
      script.addEventListener("load", this.setLoaded);
      document.body.appendChild(script);
    } else {
      //-a("Hola");
    }
  },
  created() {
    this.$store.dispatch("closeMenu");
    this.$store.dispatch("getCourse");
  },
};
</script>

<style scoped>
.login {
  background-color: var(--secondary-color);
  padding: 5px 15px 3px 15px;
  border-radius: 5px;
  text-align: center;
  font-size: 1.8rem;
  cursor: pointer;
  transition: all 0.3s;
}
.login:hover {
  filter: brightness(1.2);
}
.login a {
  text-decoration: none;
  color: white;
}

.header-lession {
  display: grid;
  width: 100%;
  margin-top: 5rem;
  grid-template-areas: "info video";
}
.header-info {
  grid-area: info;
  width: 100%;
  padding-right: 10rem;
}
.header-lession-video-container {
  grid-area: video;
  width: 100%;
}

.header-lession-target ul {
  padding-left: 2rem;
}

ul {
  padding: 0;
}
.body-lession {
  margin-top: 5rem;
}
.body-lession ul {
  margin-top: 4rem;
}
@media screen and (max-width: 768px) {
  .header-lession {
    grid-template-areas: "video" "info";
  }
  .header-info {
    padding-right: 0;
  }
  img {
    width: 100%;
  }
}
</style>