<template>
  <v-wrapper>
    <section class="register">
      <form @submit.prevent="submitForm">
        <base-title centered>Crear Cuenta</base-title>
        <div class="form-control">
          <label for="name">Nombre</label>
          <input type="text" name="nombre" id="name" v-model="name" required />
        </div>
        <div class="form-control">
          <label for="email">Correo Electronico</label>
          <input
            type="email"
            name="correo"
            id="email"
            v-model="email"
            required
          />
        </div>
        <div class="form-control">
          <label for="password">Contraseña</label>
          <input
            type="password"
            name="correo"
            id="password"
            v-model="password"
            required
          />
        </div>
        <div class="form-control">
          <label for="password">Contraseña</label>
          <input type="password" name="correo" id="password" required />
        </div>
        <button>Crear Cuenta</button>
      </form>
    </section>
  </v-wrapper>
</template>

<script>
export default {
  data() {
    return {
      name: null,
      email: "",
      password: "",
      formIsValid: null,
    };
  },
  methods: {
    submitForm() {
      this.formIsValid = true;
      if (
        this.email === "" ||
        !this.email.includes("@") ||
        this.password.length < 6
      ) {
        this.formIsValid = false;
        return;
      }
      this.$store.dispatch("signup", {
        email: this.email,
        password: this.password,
        username: this.name,
      });
      this.$router.replace("/iniciar-seccion");
    },
  },
};
</script>

<style scoped>
h2 {
  margin-bottom: 5rem;
}
.register {
  display: flex;
  justify-content: center;
}
form {
  max-width: 45rem;
  width: 100%;
}
.form-control {
  margin: 2rem 0;
  width: 100%;
}
label {
  font-size: 1.7rem;
  font-weight: 700;
  color: #6f6f73;
  margin-bottom: 1.7rem;
  display: block;
}
input {
  width: 100%;
  padding: 1.8rem;
  color: #2a6fb4;
  font-size: 1.7rem;
  background: rgba(239, 239, 239, 0.3);
  border: 1px solid rgba(133, 133, 133, 0.3);
  border-radius: 10px;
}
button {
  border: none;
  background: var(--secondary-color);
  color: #fff;
  font-size: 1.9rem;
  font-weight: 700;
  width: 100%;
  padding: 1.6rem 0;
  text-align: center;
  border-radius: 10px;
  cursor: pointer;
}
button:hover {
  filter: brightness(1.2);
}
</style>