<template>
  <header>
    <v-wrapper>
      <nav>
        <router-link to="/" class="logo">
          <fox-grow :width="'30'" :height="'30'" />
        </router-link>
        <div class="menu">
          <desktop-navegation v-if="!mobilNavBar"></desktop-navegation>
          <ul>
            <!-- <li><i class="fas fa-search"></i></li>
            <li><i class="fas fa-shopping-cart"></i></li> -->
            <li v-if="mobilNavBar">
              <button @click="toggleNavVisible">
                <i class="fas fa-bars"></i>
              </button>
            </li>
          </ul>
        </div>
        <movil-navegation :navVisible="navVisible" />
      </nav>
    </v-wrapper>
  </header>
</template>

<script>
import FoxGrow from "../../icons/FoxGrow.vue";
import DesktopNavegation from "./DesktopNavegation.vue";
import MovilNavegation from "./MovilNavegation.vue";
export default {
  components: {
    FoxGrow,
    MovilNavegation,
    DesktopNavegation,
  },
  data() {
    return {
      navVisible: false,
    };
  },
  computed: {
    mobilNavBar() {
      return this.$store.getters["navBarSize"];
    },
  },
  methods: {
    toggleNavVisible() {
      this.$store.dispatch("openMenu");
    },
  },
};
</script>

<style scoped>
header {
  background-color: var(--primary-color);
  position: sticky;
  top: -1px;
  padding: 0.5rem 0;
  z-index: 200;
}
nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
ul {
  display: flex;
  list-style: none;
  align-items: center;
  margin: 0;
}

li {
  padding: 1.5rem;
}

button,
.router-link-active {
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  position: relative;
}

button:focus,
.router-link-active:focus {
  outline: none;
}
button:focus::before {
  content: "";
  position: absolute;
  width: 35px;
  height: 35px;
  background: rgba(255, 255, 255, 0.3);
  top: -25%;
  left: -33%;
  z-index: -1;
  border-radius: 50%;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: center;
}

i {
  font-size: 1.8rem;
  color: var(--light-grey);
}
.fa-bars {
  color: var(--secondary-color);
  transition: all 0.2s ease;
}
.fa-bars:hover {
  color: var(--light-grey);
}
</style>