<template>
  <v-wrapper>
    <section class="login">
      <div class="form-login">
        <base-title centered>Iniciar Seccion</base-title>
        <form @submit.prevent="loggin">
          <p v-if="errorMessage" class="error">{{ errorMessage }}</p>
          <div class="form-control">
            <label for="email">Correo Electronico</label>
            <input
              type="email"
              name="correo"
              id="email"
              v-model="username"
              required
            />
          </div>
          <div class="form-control">
            <label for="password">Contraseña</label>
            <input
              type="password"
              name="correo"
              id="password"
              v-model="password"
              required
            />
          </div>
          <button>Iniciar Seccion</button>
        </form>
        <p>
          <router-link to="/auth/reset-password"
            >Olvide mi contrasena</router-link
          >
        </p>
        <p>
          No tienes cuenta?
          <router-link to="/registro">Crear Cuenta</router-link>
        </p>
      </div>
    </section>
  </v-wrapper>
</template>

<script>
export default {
  data() {
    return {
      username: null,
      password: null,
      message: null,
    };
  },
  computed: {
    errorMessage() {
      return this.message;
    },
  },
  methods: {
    async loggin() {
      try {
        await this.$store.dispatch("login", {
          email: this.username,
          password: this.password,
        });
        this.$router.replace("/");
      } catch (err) {
        //-a(err);
        if (err === "auth/too-many-requests") {
          this.message =
            "Haz realizados demasiados intentos, vuelva a intentar mas tarde";
        }
        if (err !== "auth/too-many-requests") {
          this.message = "El usuario y/o contrasena no son correctos";
        }
      }
    },
  },
  created() {
    this.$store.dispatch("closeMenu");
  },
};
</script>

<style scoped>
h2 {
  font-weight: 900;
  margin-bottom: 5rem;
}
.login {
  display: flex;
  justify-content: center;
  /* place-content: center; */
}
.form-login {
  max-width: 45rem;
  width: 100%;
}
.form-control {
  margin: 2rem 0;
  overflow: hidden;
  width: 100%;
}
label {
  font-size: 1.7rem;
  font-weight: 700;
  color: #6f6f73;
  margin-bottom: 1.7rem;
  display: block;
}
input {
  width: 100%;
  padding: 1.8rem;
  color: #2a6fb4;
  font-size: 1.7rem;
  background: rgba(239, 239, 239, 0.3);
  border: 1px solid rgba(133, 133, 133, 0.3);
  border-radius: 10px;
}
button {
  border: none;
  background: var(--primary-color);
  color: #fff;
  font-size: 1.9rem;
  font-weight: 700;
  width: 100%;
  padding: 1.6rem 0;
  text-align: center;
  border-radius: 10px;
  cursor: pointer;
}
p {
  font-size: 1.7rem;
  font-weight: 500;
  color: #110d3c;
  text-align: center;
}
a {
  color: var(--secondary-color);
}
.error {
  color: red;
  font-size: 1.2;
  text-align: left;
}
</style>