export default {
  state() {
    return {
      productCharge: false,
      videos: [],
      purchasedCourse: false
    };
  },
  mutations: {
    setCourse(state, payload) {
      state.videos.push(payload);
      state.productCharge = true;
    },
    setPurchase(state, payload) {
      state.purchasedCourse = payload;
      //-a(payload);
    }
  },
  actions: {
    changePurchasedStatus(context, payload) {
      //-a('entrar');
      context.commit('setPurchase', payload.status);
    },
    async getCourse(context) {
      const userid = context.rootState.auth.userId;
      if (userid) {
        try {
          const response = await fetch(
            `http://localhost:1337/paypals?_where[user_recid.id]=${userid}`
          );
          const data = await response.json();
          //-a(data[0]);
          if (data[0].id) {
            context.commit('setPurchase', true);
          }
        } catch (err) {
          //
        }
      }

      return new Promise((resolve, reject) => {
        if (!context.state.productCharge) {
          fetch('http://localhost:1337/cursos')
            .then((response) => response.json())
            .then((data) => {
              return fetch(
                `http://localhost:1337/modules?curso.id=${data[0].id}`
              );
            })
            .then((moduleResponse) => moduleResponse.json())
            .then((moduleData) => {
              let data = { module: '', id: '', lecciones: [] };
              for (let module of moduleData) {
                data.module = module.name;
                data.id = module.id;
                for (let video of module.videos) {
                  data.lecciones.push({
                    name: video.name,
                    videoUrl: video.video_url,
                    id: video.video_order,
                    duration: video.video_duration
                  });
                }
                data.lecciones.sort(function(a, b) {
                  if (a.id > b.id) {
                    return 1;
                  }
                  if (a.id < b.id) {
                    return -1;
                  }
                  return 0;
                });
                context.commit('setCourse', data);
                data = { module: '', id: '', lecciones: [] };
              }
            })
            .then(() => {
              return resolve(context.state.videos);
            })
            .catch((err) => {
              console.log(err);
              return reject;
            });
        } else {
          //-a(context.state.videos);
          resolve(context.state.videos);
          return;
        }
      });
    }
  },
  getters: {
    videos(state) {
      return state.videos;
    },
    productCharge(state) {
      return state.productCharge;
    },
    purchasedCourse(state) {
      return state.purchasedCourse;
    }
  }
};
