<template>
  <div class="nav">
    <ul>
      <li>
        <router-link to="/"> Inicio </router-link>
      </li>
      <li>
        <router-link :to="{ name: 'Course', params: { id: 1 } }">
          Cursos
        </router-link>
      </li>
      <li>
        <router-link to="/nosotros"> Nosotros </router-link>
      </li>
      <li>
        <router-link to="preguntas-frecuentes"> FAQs </router-link>
      </li>
      <li class="login" v-if="isAuth">
        <router-link :to="{ name: 'Login' }"> iniciar Seccion </router-link>
      </li>
      <li class="auth" v-if="!isAuth">
        <p><i class="fas fa-user"></i>{{ username }}&nbsp;&nbsp;|</p>
        <button @click="logout">Cerra Seccion</button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      auth: null,
    };
  },
  watch: {
    $route() {
      //this.auth = firebase.auth().currentUser ? false : true;
      this.$forceUpdate();
    },
  },
  computed: {
    isAuth() {
      return this.$store.getters.userId ? false : true;
    },
    username() {
      return this.$store.getters.name;
    },
  },
  methods: {
    changeset() {
      //-a(window.localStorage.getItem("UserName"));
      this.auth = window.localStorage.getItem("UserId") ? false : true;
    },
    logout() {
      this.$store.dispatch("logout");
      this.$forceUpdate();
      this.$router.replace("/");
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.changeset();
      //this.auth = firebase.auth().currentUser ? false : true;
      // this.$forceUpdate();
    });
  },
};
</script>

<style scoped>
ul {
  display: flex;
  list-style: none;
  align-items: center;
  margin: 0;
}

li {
  padding: 1.5rem;
}

i {
  font-size: 1.8rem;
  color: var(--light-grey);
}

button {
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  position: relative;
}
.nav {
  z-index: 5;
}

.nav ul {
  display: flex;
  align-items: center;
  margin-left: -3.5rem;
}

.nav a {
  font-size: 1.4rem;
  display: block;
  color: var(--white);
  /* margin-left: 2rem; */
  padding-bottom: 0.5rem;
  text-decoration: none;
}

li .router-link-active {
  color: var(--secondary-color);
  font-weight: 700;
  position: relative;
}

.router-link-active::after {
  content: "";
  position: absolute;
  display: block;
  width: 1.2rem;
  height: 0.2rem;
  border-radius: 8px;
  background-color: var(--secondary-color);
  top: 90%;
  left: 48%;
  transform: translateX(-50%);
}
.nav > button {
  position: relative;
  margin: 2rem;
  top: 7px;
  left: 75%;
}
.nav-active .fa-times {
  transform: rotate(-180deg);
  transition: transform 0.5s ease 0.8s;
}
.fa-times {
  color: var(--white);
}

.login {
  background-color: var(--secondary-color);
  padding: 5px 15px 3px 15px;
  border-radius: 5px;
  margin-left: 30px;
}
.login a {
  color: white;
}

.auth {
  display: flex;
  align-items: center;
  margin-bottom: 7px;
}
.auth p {
  font-size: 1.6rem;
  color: white;
  margin: 0;
}
.fa-user {
  margin-right: 8px;
  position: relative;
  top: 2px;
}

.auth button {
  font-size: 1.6rem;
  color: white;
  cursor: pointer;
  padding: 7px 10px;
}

.auth button:hover {
  background: rgba(255, 255, 255, 0.2);
}
</style>