<template>
  <teleport to="body">
    <base-dialog
      v-if="inputContactInfo"
      title="Contáctanos"
      @close="viewContactInfo"
    >
      <template #default>
        <base-description>
          Para solicitar nuestros servicios personalizados puedes contactarnos a
          través de las siguientes vías:
        </base-description>
        <div class="contact">
          <p><i class="fab fa-whatsapp"></i> (809) 855-4849</p>
          <p><i class="far fa-envelope"></i> staff@foxgrowacademy.com</p>
        </div>
      </template>
      <template #actions>
        <base-button @click="viewContactInfo">Cerrar</base-button>
      </template>
    </base-dialog>
  </teleport>
  <base-card>
    <img :src="imageUrl" :alt="title" />
    <base-title font-size="25">{{ title }}</base-title>
    <base-description font-size="1.7">
      {{ description }}
    </base-description>
    <button class="btn-service" @click="viewContactInfo">
      click me <i class="fas fa-arrow-right"></i>
    </button>
  </base-card>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      required: true,
    },
    description: {
      type: String,
      required: true,
    },
    imageUrl: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      inputContactInfo: false,
    };
  },
  methods: {
    viewContactInfo() {
      this.inputContactInfo = !this.inputContactInfo;
    },
  },
};
</script>

<style scoped>
div {
  height: 100%;
}
img {
  width: 100%;
}
.btn-service {
  border: none;
  background: transparent;
  color: var(--secondary-color);
  font-size: 1.8rem;
  cursor: pointer;
}
.btn-service:hover {
  background: var(--lighter-grey);
  text-decoration: underline;
}
.fa-whatsapp,
.fa-envelope {
  color: var(--secondary-color);
  font-size: 3rem;
}
.contact {
  font-size: 2rem;
  color: #110d3c;
}

@media screena and (max-width: 600px) {
  .contact p {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
</style>